.app{
    height: 100vh;
}

.sections{
    width: 100%;
    height: calc(100vh - 10%);
    position: relative;
    top: 10%;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;
    &::-webkit-scrollbar{
        display: none;
    }
    > *{
        width: 100vw;
        height: calc(100vh - 10%);
        scroll-snap-align: start ;
    }
}




